<template>
    <div class="hotMap" ref="hotMap">
        123
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  var hours = ['办公室 ','合规与监审部','系统规划部','资产管理部','市场营销部','安全监管部','人力资源部','党建工作部','创新与数字化部','财务部']
  var days = ['整改','问责','确认','取证','派工'];

  var data = [
    [0, 0, 1],
    [0, 1, 0],
    [0, 2, 1],
    [0, 3, 1],
    [0, 4, 1],

    [1, 0, 0],
    [1, 1, 0],
    [1, 2, 0],
    [1, 3, 1],
    [1, 4, 2],

    [2, 0, 2],
    [2, 1, 1],
    [2, 2, 1],
    [2, 3, 1],
    [2, 4, 2],

    [3, 0, 3],
    [3, 1, 2],
    [3, 2, 3],
    [3, 3, 2],
    [3, 4, 3],

    [4, 0, 3],
    [4, 1, 2],
    [4, 2, 3],
    [4, 3, 4],
    [4, 4, 5],

    [5, 0, 4],
    [5, 1, 3],
    [5, 2, 4],
    [5, 3, 3],
    [5, 4, 4],

    [6, 0, 2],
    [6, 1, 2],
    [6, 2, 2],
    [6, 3, 2],
    [6, 4, 2],

    [7, 0, 2],
    [7, 1, 1],
    [7, 2, 1],
    [7, 3, 2],
    [7, 4, 1],

    [8, 0, 3],
    [8, 1, 3],
    [8, 2, 3],
    [8, 3, 2],
    [8, 4, 2],

    [9, 0, 4],
    [9, 1, 3],
    [9, 2, 4],
    [9, 3, 4],
    [9, 4, 4],
  ];


  let option = {
    // tooltip: {
    //   position: 'top'
    // },
    animation: false,
    tooltip: {
      // trigger: 'axis',
      backgroundColor: 'rgba(42, 92, 176, .6)',
      borderColor: "rgba(0, 255, 239, 1.0)",
      textStyle: {
        color: '#00FFEF'
      },
      axisPointer: {
        type: 'cross',
        crossStyle: {
          color: '#999'
        }
      }
    },
    grid: {
      left: '3%',
      top: '10%',
      right: '3%',
      bottom: '30',
      containLabel: true,
      // height: '50%',
      // top: '10%'
    },
    xAxis: {
      type: 'category',
      data: hours,
      splitArea: {
        show: true
      },
      axisTick: {
        lineStyle: {
          color: '#c4c4c4'
        }
      },
      axisLabel: { //设置x轴的字
        show: true,
        interval:0,//使x轴横坐标全部显示
        // rotate:50,//旋转
        textStyle: {//x轴字体样式
          color: "#fff",
          margin: 15,
          fontWeight: 'bold',
        }
      },
    },
    yAxis: {
      type: 'category',
      data: days,
      splitArea: {
        show: true
      },
      axisTick: {
        show: false
      },
      axisLabel: { //设置y轴的字
        // show: true,
        // formatter: '{value} ml',
        color:'#fff',
        interval: 0,//使y轴横坐标全部显示
        textStyle: {//y轴字体样式
          color: "#fff",
          margin: 15,
        }
      },
    },
    visualMap: {
      show:false,
      min: 0,
      max: 5,
      calculable: true,
      orient: 'horizontal',
      left: 'center',
      bottom: '15%',
      color: ['#00236a','#1877b4','#11a8ff'],
      borderWidth:'2',
      borderColor:'#ff8000'
    },
    series: [{
      name: '',
      type: 'heatmap',
      data: data,
      label: {
        show: true
      },
      borderColor: '#ff8000',
      emphasis: {
        itemStyle: {
          // color:'#ffffff',
          shadowBlur: 10,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      }
    }]
  };
  export default {
    name: "hotMap",
    data(){
      return {
        hotOption:option,
      }
    },
    created() {
    },
    mounted() {
      this.init()
    },
    methods:{
      init() {
        let mychart = echarts.init(this.$refs.hotMap, );
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.hotOption,true);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });
        window.addEventListener("resize", ()=>{
          mychart.resize()
        });

      },
    }
  }
</script>

<style scoped>
    .hotMap{
      width: 100%;
      height: 100%;
    }
</style>